[data-theme=inntheme] {
  --base-color: #06b5d4;
  --secondary-color: #E6F7FA;
  --navbar-bg: white;
  --brand-color: var(--base-color);
  --primary-color: var(--base-color);
  --primary: var(--base-color);
  --highlight-color: var(--secondary-color);
  --table-header: var(--primary);
  --bg-color: var(--gray-200);
  --heading-color: var(--base-color);
  --scrollbar-color: var(--secondary-color);
  --field-border: var(--base-color);
  --field-label: var(--base-color);
  --input-disabled-bg: var(--gray-100);
  --disabled-control-bg: var(--gray-100);
  --disabled-text-color: var(--gray-800);
  --side-section-background-color: white;
  --workspace-menu-color: white;
  --sidebar-select-color: var(--gray-200);
  --side-section-menu-text-color: var(--gray-600);
  --sidebar-hover-color: var(--base-color);
  --sidebar-hover-text-color: white;
  --sidebar-selected-color: var(--base-color);
  --side-section-menu-selected-text-color: white;
  --side-section-text-and-icon-color: var(--gray-800);
  --skeleton-bg: white;
  --navbar-text-color: black;
  --navbar-icon-color: black;
  --form-control-shadow: none;
  --border-size: 1px;
  --form-control-focus-shadow: 0 0 0 var(--border-size) var(--field-border);
  --links-widget-bg: white;
  --links-widget-text-color: var(--text-color);
  --links-widget-text-hover-color: black;
  --widget-title-hover-color: var(--heading-color);
  --nav-link-active-color: var(--base-color);
  --workspace-border-color: var(--base-color);
}
[data-theme=inntheme] .head-title,
[data-theme=inntheme] .comment-box .comment-input-header,
[data-theme=inntheme] .form-section .section-head,
